import React, { Component } from 'react';
import axios from 'axios';

class AddSeries extends Component {

  constructor(props){
    super(props);

    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeRating = this.onChangeRating.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onChangePicture = this.onChangePicture.bind(this);
    this.onChangePages = this.onChangePages.bind(this);
    this.onChangeName = this.onChangeName.bind(this);
    this.onChangeTags = this.onChangeTags.bind(this);



    this.state = {
      title: '',
      rating: '',
      picture: '',
      chapters : {
        chapter:{
            name:'',
            images: ''
        }
      },
      tags: ''
    }

  };

  onChangeTitle(e){
    this.setState({
      title: e.target.value
    });
  }
  onChangeRating(e){
    this.setState({
      rating: e.target.value
    });
  }
  onChangePicture(e){
    this.setState({
      picture: e.target.value
    });
  }

  onChangePages(e){
    this.setState({
      chapters: {
          chapter:{
              name: this.state.chapters.chapter.name,
              images: e.target.value
          }
       
    }
    });
  }
  onChangeName(e){
    this.setState({
        chapters: {
            chapter:{
                images: this.state.chapters.chapter.images,
                name: e.target.value
            }
        }
    });
  }
  onChangeTags(e){
    this.setState({
        tags : e.target.value

    });
  }

  onSubmit(e){
    e.preventDefault();

    let formattedTags = []
    let formattedChapters = {
      chapter:[{
          name: this.state.chapters.chapter.name,
          images: []
      }]
    }
    let x = []
    let y = []
    x.push(this.state.chapters.chapter.images.split(','));
    y.push(this.state.tags.split(','));
    formattedChapters.chapter[0].images = x[0];
    formattedTags = y[0];
    console.log(formattedChapters);

    //if series doesnt exist add it

    const newSeries = {
    title: this.state.title.trim(),
    rating: this.state.rating.trim(),
    picture: this.state.picture.trim(),
    chapters: formattedChapters.chapter,
    tags: formattedTags
  }
  axios.post('http://localhost:4000/img1/add',newSeries).then(res =>{
    console.log(res.data);
    this.setState({
      title: '',
      rating: '',
      picture: '',
      chapters: {
         chapter:{
             name:'',
             images:''
         }
      },
      tags: ''

    });

  }).catch(err=>{
    console.log(err);
  });


 
}

  componentDidMount(){

  }

  render(){
      return (
      <div className="container-fluid">
         <div className="content">
           <form onSubmit={this.onSubmit}>
             <div className="form-group">
             <h3>Add Series Page</h3>
            
               <label><p>Series Name:</p></label>
               <input type="text" className="form-control" value={this.state.title} onChange={this.onChangeTitle} placeholder="EX. Anime"/>
               <label><p>Rating:</p></label>
               <input type="number" style={this.state.style} className="form-control" value={this.state.rating} onChange={this.onChangeRating} placeholder="EX. 1 - 5"/>
               <label><p>Series Main Picture:</p></label>
               <input type="text" style={this.state.style} className="form-control" value={this.state.picture} onChange={this.onChangePicture} placeholder="EX. https://i.imgur.com/0cFuHz8.jpg"/>
               <label><p>Pages Links:</p></label>
               <input type="text" style={this.state.style} className="form-control" value={this.state.chapters.pages} onChange={this.onChangePages} placeholder="EX. paint,nails,ect..."/>
               <label><p>Chapter Name:</p></label>
               <input type="text" style={this.state.style} className="form-control" value={this.state.chapters.name} onChange={this.onChangeName} placeholder="EX. 1 - 10000"/>
               <label><p>Tags:</p></label>
               <input type="text" style={this.state.style} className="form-control" value={this.state.tags} onChange={this.onChangeTags} placeholder="EX. Mexican,Asian,English"/>
               <input type="submit" value="Add Series" className="btn btn-primary"/>
             </div>
           </form>
         </div>
      </div>
  );
}

}

export default AddSeries;