import React, { Component } from 'react';
import {withRouter} from 'react-router-dom';
import axios from 'axios';

class DisplayChapter extends Component {

  constructor(props){
    super(props);

    this.onSubmit = this.onSubmit.bind(this);
    this.onKey = this.onKey.bind(this);
    this.changeChap = this.changeChap.bind(this);
    this.selectElement = this.selectElement.bind(this);

    

    this.state = {
        series: [],
        title: this.props.match.params.series,
        chapterIndex: this.props.match.params.index,
        test: this.props.match.params.index,
        rating: '',
        picture: '',
        chapters : {
          chapter:{
              name:'',
              images: ''
          }
        },
        tags: ''
    }
    
  };

  noSeriesFound(){
    //console.log(e.target.id);
    alert('This series or chapter doesnt seem to exist please try again...')

  
    this.props.history.push({
      pathname: `/Series-List`,
    });
  }
  
  onSubmit(e){
      e.preventDefault();
      let number = parseInt(this.state.chapterIndex)
      if(number+1 < this.state.series[0].chapters.length){
        this.setState({
          chapterIndex: number+1
        })
        window.scrollTo(0, 0);
      }else{
        alert('This is the latest chapter of the series...')
      }

  }

  selectElement(valueToSelect) {    
     let element = document.getElementById("chapterList");
     element.value = valueToSelect;
 }
 
  changeChap(e){
    if(e.target.value !== "Select Chapter"){
      this.setState({
      chapterIndex: e.target.value
    })
    } 
    this.selectElement(e.target.value);

}
  onKey(e){
    let number = parseInt(this.state.chapterIndex)
    
    if(e.keyCode === 39){
      // console.log('Next Chapter code 39');
      if(number+1 < this.state.series[0].chapters.length){
        this.setState({
          chapterIndex: number+1
        });
        window.scrollTo(0, 0);
      }
      
    }
    if(e.keyCode === 37){
      console.log('Previous Chapter');
      if(number-1 !== -1){
        this.setState({
          chapterIndex: number-1
        })
        window.scrollTo(0, 0);
      }
    }

}


  componentDidMount(){
    this.setState(()=>({ 
      title : this.props.match.params.series
    }),()=>{
      console.log(this.state.title);
      axios.get(`http://localhost:4000/img1/seriesOne/${this.state.title}`).then(res=>{
        // console.log(res.data[0].chapters[this.state.chapterIndex]);
        // console.log(new Date(res.data[0].chapters[this.state.chapterIndex].date).getTime())
        
        let checker ={};
        // let checker2 ={};
        // res.data[0].chapters.map((chapter,index)=>{          
        //   checker[index] = chapter.name;
        // });

        res.data[0].chapters.forEach((element,index) => {
          // console.log(element,index)
          checker[index] = element.name;
          
        });

        // console.log(checker);
        // console.log(checker2);
        if(res.data[0].chapters.length <= this.state.chapterIndex){
          this.noSeriesFound();
        };
        let x = 0;
        for (x in checker){
          if(checker[x] === `Chapter ${this.state.chapterIndex}`|| checker[x] === `Chapter${this.state.chapterIndex}`){
            this.setState({
              chapterIndex: x
            });
          };
          x++;
        }

          this.setState({
              series: res.data,
             
          });
      });
      
    });

    document.addEventListener("keydown", this.onKey, false);

  }

  render(){
      return (
      <div className="container-fluid">
         <div className="content">
             {this.state.series.map((series,index)=>{
                 return(
                    <div key={index}>
                      <h3>{series.title.toUpperCase()} {series.chapters[this.state.chapterIndex].name.toUpperCase()}</h3>
                      <h3>
                        <select id="chapterList" onChange={this.changeChap}>
                          <option>Select Chapter</option>
                          {series.chapters.map((chapter,index)=><option key={index} value={index}>{chapter.name}</option>)}
                        </select>
                      </h3>                  

                     {series.chapters[this.state.chapterIndex].images.map((picture,index)=>{
                         return(
                             <img src={picture} alt={series.title} key={index} className="fullSizedPic"></img>
                         )
                     })}                                      
                     </div>
                 )                 
             })}

             <button onClick={this.onSubmit}>Next Chapter</button>
         </div>
      </div>
  );
}

}

export default withRouter(DisplayChapter);