import React, { Component } from 'react';
import {withRouter} from 'react-router-dom';
import axios from 'axios';
var Papa = require('papaparse');


class EditSeries extends Component {

  constructor(props){
    super(props);

    this.onSubmit = this.onSubmit.bind(this);
    this.onChangeName = this.onChangeName.bind(this);
    this.onChangePages = this.onChangePages.bind(this);

    this.state = {
        series: [],
        series2: [],
        title: this.props.location.state.title,
        rating: '',
        picture: '',
        chapters : {
          chapter:{
              name:'',
              images: ''
          }
        },
        tags: '',
        csvData : []
    }
    
  };
  onChangeName(e){
    this.setState({
        chapters: {
            chapter:{
                images: this.state.chapters.chapter.images,
                name: e.target.value
            }
        }
    });
  }

  onChangePages(e){
    this.setState({
      chapters: {
          chapter:{
              name: this.state.chapters.chapter.name,
              images: e.target.value
          }  
      }
    });
  }

  onSubmit(e){
    e.preventDefault();
    let file = document.getElementById('csv').files[0]
    console.log('file size',file.size);
    
    let temptitle = this.state.title;
    let y = this.state.series[0];

   
 

    Papa.parse(file,{
      complete: function(results){
        for(let x of results.data){

          let temp = [];
          let temp2 = {
            images:[],
            _id: "5d321a10a90abd58c8a48e81",
            name: ''
          }

          if(x[0] !== ''){
            if(x[0].length > 8){

                //set chapter name
                temp2.name = x[0]
                //format data                
                temp.push(x[1].split(','));
                //set imgs
                temp2.images = temp[0];
                y.chapters.push(temp2)
     
            } 
          }
        }
        
        for(let i of y.chapters){
          i.images.pop()
        }
        // console.log(y.chapters)
        // post to the database
        axios.post(`http://localhost:4000/img1/update/chapter/all/${temptitle}`,y).then(res=>{
          console.log(res)
        }).catch(err=>{
          console.log(err)
        });
      }
    })
    

      // let y = this.state.series[0];
      // console.log(y.chapters[0])
      // y.chapters[0].name = this.state.chapters.chapter.name;
      // let x = [];
      // x.push(this.state.chapters.chapter.images.split(','));
      // y.chapters[0].images = x[0];

   
    
  }
  

  componentDidMount(){
    axios.get(`http://localhost:4000/img1/seriesOne/${this.state.title}`).then(res=>{
      console.log(res.data)
        this.setState({
            series: res.data,
            series2: res.data
           
        });
    });

  }

  render(){
      return (
      <div className="container-fluid">
         <div className="content">
        <h3>Series: </h3>
        {this.state.series.map((series,index)=>{
            return(
                    <img src={series.picture} key={index} alt={series.name} className="seriesPictures"></img>
            )
        })}
        <form onSubmit={this.onSubmit}>
             <div className="form-group"> 
               <label><p>Chapter Name:</p></label>
               <input type="text" style={this.state.style} className="form-control" value={this.state.chapters.name} onChange={this.onChangeName} placeholder="EX. Bob's world"/>         
               <label><p>Pages Links:</p></label>
               <input type="text" style={this.state.style} className="form-control" value={this.state.chapters.pages} onChange={this.onChangePages} placeholder="EX. paint,nails,ect..."/>
               <input type ='file' id='csv'></input>
               <input type="submit" value="Add Chapter" className="btn btn-primary"/>
             </div>
           </form>

         </div>
      </div>
  );
}

}

export default withRouter(EditSeries);