import React, { Component } from 'react';
import {withRouter} from 'react-router-dom';
import axios from 'axios';


class UpdateSeries extends Component {

  constructor(props){
    super(props);

    this.onSubmit = this.onSubmit.bind(this);

    this.state = {
        series: []
    
    }

  };

  onSubmit(e){
      e.preventDefault();
      console.log(e.target.id);
      this.props.history.push({
        pathname: `/Edit-Series`,
        state: { 
            title: e.target.id
        }
      });
  }

  componentDidMount(){
    axios.get('http://localhost:4000/img1').then(res=>{
        this.setState({
            series: res.data
        });
    });

  }

  render(){
      return (
      <div className="container-fluid">
         <div className="content">
        <h3>update series page</h3>
        {this.state.series.map((series,index)=>{
            return(
                <img src={series.picture} key={index} alt={series.name} onClick={this.onSubmit} id={series.title} className="seriesPictures"></img>
            )
        })}
         </div>
      </div>
  );
}

}

export default withRouter(UpdateSeries);