import React, { Component } from 'react';
import axios from 'axios';
import {withRouter} from 'react-router-dom';


class Toprated extends Component {

  constructor(props){
    super(props);

    this.changeRating = this.changeRating.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onSubmit2 = this.onSubmit2.bind(this);
    this.onChangeYear = this.onChangeYear.bind(this);
    this.onChangeMonth= this.onChangeMonth.bind(this);
    this.onChangeDay = this.onChangeDay.bind(this);
    this.onChangeDate = this.onChangeDate.bind(this);





    
    this.state = {
      rating: "all",
      series: [],
      year: "",
      month: "",
      day: "",
      minutes: "",
      seconds: "",
      date: "",
      date2: ""
      
    }

  };

  changeRating(e){
    let num = e.target.value;

    this.setState(()=>({
      rating: num
    }),()=>{  
      // console.log(this.state.rating)
      this.checkDatabase();
    })
  }
  onChangeYear(e){
    this.setState({
      year: e.target.value
    });
  }
  onChangeMonth(e){
    this.setState({
      month: e.target.value
    })
  
  }
  onChangeDay(e){
    this.setState({
      day: e.target.value
    });
  }
  onChangeDate(e){
    this.setState({
      date: e.target.value
    });
  }

  onSubmit2(e){
   e.preventDefault();

   let date2 = new Date(this.state.date +"EST")
  //  this.setState({
  //    date:date2
  //  })

    this.setState(()=>({
      date2: date2
    }),()=>{  
      // console.log(this.state.date);   
    })
   
  }




  onSubmit(e){
    e.preventDefault();
    //console.log(e.target.id);
    this.props.history.push({
      pathname: `/Series-List/Chapter-List/${e.target.id}`,
      state: { 
          title: e.target.id
      }
    });
  }

  checkDatabase(){
     //get series from database (all)
      axios.get(`http://localhost:4000/img1/rating/${this.state.rating}`).then(res =>{
        console.log(res.data)
        //set state equal the response from the database
        this.setState({
          series : res.data
        });
      }).catch(err=>{
        console.log(err);
    });
  }


  componentDidMount(){
    this.checkDatabase();

  }

  render(){
      return (
      <div className="container-fluid">
         <div className="content">
           <h1>TOP RATED</h1> 
            <h3>
              Filter by Rating <select id="selectRating" onChange={this.changeRating}>
                <option value="all" >Show All</option>
                <option value="1" >1</option>
                <option value="2" >2</option>
                <option value="3" >3</option>
                <option value="4" >4</option>
                <option value="5" >5</option>
              </select>
            </h3>
            <form onSubmit={this.onSubmit2}>
              <label className="label2">Added After -- </label>
              <input type="date" id="date" name="date" value={this.state.date} onChange={this.onChangeDate}></input>
              <input type="submit" value="Check Date" className="btn btn-success"/>
            </form>
            <div>{this.state.series.map((series,index)=>{
              if(new Date(series.date).getTime()>new Date(this.state.date2).getTime()){
                return(
                <h3 key={index}>{series.date} ----- {series.title}</h3>
              )
              }else{
                return(
                  <h3 key={index}>None</h3>
                )
              }
              
            })}</div>
            {this.state.series.map((series,index)=>{
               return(
                 <div key={index} className="inLine">
                    <img src={series.picture}  onClick={this.onSubmit} id={series.title }alt={series.title} className="seriesPictures"></img>
                    <div className="extraInfo" id={'extraInfo'+series.name}> 
                      Name of Series: {series.title.toLowerCase()}<br/>
                      Number of Chapters: {series.chapters.length}<br/>
                      Tags: {series.tags + ' '}<br/>
                      Rating: {series.rating + ' '}<br/>
                    </div>
                 </div>                
               )
             })}
         </div>
      </div>
  );
}

}

export default withRouter(Toprated);