import React, { Component } from 'react';
import {withRouter} from 'react-router-dom';
import axios from 'axios';

class ChapterList extends Component {

  constructor(props){
    super(props);

    this.onSubmit = this.onSubmit.bind(this);
    this.changeStyle = this.changeStyle.bind(this);


    this.state = {
        series: [],
        title: this.props.match.params.series,
        rating: '',
        picture: '',
        chapters : {
          chapter:{
              name:'',
              images: ''
          }
        },
        tags: ''
    }
    
  };


  onSubmit(e){
      e.preventDefault();
      //console.log(e.target.id);
      this.props.history.push({
        pathname: `/Series-List/Chapter-List/${this.state.title}/${e.target.id}`,
        state: { 
            title: this.state.title,
            chapterIndex: e.target.id
        }
      });

  }
  changeStyle(e){
    let x = document.getElementById(`list${this.state.series[0].title}`);
    let y = document.getElementById(`test1`);
    // console.log(`list`+`${this.state.series[0].title}`)

    // console.log(x);
    try{
      if (x.style.visibility === "visible") {
      x.style.visibility = "collapse";
      y.style.visibility = "visible";
    } else {
      x.style.visibility = "visible";
    }
    }catch{
      console.log("an error has occured")
    }
   

}

  noSeriesFound(){
    //console.log(e.target.id);

    this.props.history.push({
      pathname: `/Series-List`,
    });

}

  componentDidMount(){
    axios.get(`http://localhost:4000/img1/seriesOne/${this.state.title}`).then(res=>{
    
      console.log(res.data)
      if(res.data.length === 0){
        //   console.log("reached")
          this.noSeriesFound();
          
      };
    //  console.log(res.data[0].chapters)
        this.setState({
            series: res.data,
           
        });
    });

    //console.log(this.props.match.params.series);


  }

  render(){
      return (
      <div className="container-fluid">
         <div className="content">
             {this.state.series.map((series,index)=>{
                 return(
                     <div className="chapterDisplay" key={index}>
                         <h3>{series.title.toUpperCase()}</h3>
                        <img src={series.picture} key={index} alt={series.name} id={series.title} className="seriesPictureChapter"></img>
                        <div className="chapterPageInfo">
                            
                            Number of Chapters: {series.chapters.length}<br/>
                            <div className="desc">
                                Description:<br/>
                                This is a Description of the series and the main points about it
                            </div>
                        </div>
                        <div className="chapterListFull1" id={'tester2'} hidden={false}>
                        <button className="displayChaptersBtn" id="test1" onClick={this.changeStyle}><h3>Display Chapters</h3></button>

                        </div>
                        <div className="chapterListFull" id={'list'+series.title} hidden={false}>
                            <h3>Chapter List</h3>
                            
                                {series.chapters.map((chapter,index)=>{
                                    return(
                                        <div key={index}>
                                            <a href='/1' onClick={this.onSubmit} id={index}>Chapter {index + 1} {chapter.title}</a>
                                        </div>
                                    )
                                })}

                            </div>
                     </div>
                 )
             })}
        </div>
      </div>
  );
}

}

export default withRouter(ChapterList);