import React, { Component } from 'react';
import {withRouter} from 'react-router-dom';

import axios from 'axios';


class Serieslist extends Component {

  constructor(props){
    super(props);

    this.onSubmit = this.onSubmit.bind(this);
    this.onChangeSearch = this.onChangeSearch.bind(this);

    
    this.state = {
        series : [],
        search: ''
    }

  };
  moveDiv(e){
    //  let x = e.screenX;
    //  let y = e.screenY;
    // document.getElementById('extraInfo'+e.target.id).style.left=x+"px";
    // document.getElementById('extraInfo'+e.target.id).style.top=y+"px";
  }
  onSubmit(e){
    e.preventDefault();
    //console.log(e.target.id);
    this.props.history.push({
      pathname: `/Series-List/Chapter-List/${e.target.id}`,
      state: { 
          title: e.target.id
      }
    });
  }
  onChangeSearch(e){
    this.setState({
      search: e.target.value
    },()=>{
      // console.log(this.state.search)
      if(this.state.search === ''){
        axios.get(`http://localhost:4000/img1/`).then(res =>{
          //   console.log(res.data);
          this.setState({
              series : res.data
          });
          // console.log(this.state.accounts);
           
          }).catch(err=>{
            console.log(err);
          });   
      }else{
        axios.get(`http://localhost:4000/img1/series/${this.state.search}`).then(res =>{
          // console.log(res.data);
          this.setState({
              series : res.data
          });
          
           
          }).catch(err=>{
            console.log(err);
          });
      }
    });

  }  


  componentDidMount(){
    //get series from database (all)
    axios.get(`http://localhost:4000/img1`).then(res =>{
        // console.log(res.data)
        //set state equal the response from the database
        this.setState({
          series : res.data
        });
    }).catch(err=>{
        console.log(err);
    });

  }

  render(){
      return (
      <div className="container-fluid">
         <div className="content">
             <h3><input onChange={this.onChangeSearch} placeholder="Search"></input></h3>  
             {this.state.series.map((series,index)=>{
               return(
                 <div key={index} className="inLine">
                    <img src={series.picture}  onClick={this.onSubmit} id={series.title }alt={series.title} className="seriesPictures"></img>
                    <div className="extraInfo" id={'extraInfo'+series.name}> 
                      Name of Series: {series.title.toLowerCase()}<br/>
                      Number of Chapters: {series.chapters.length}<br/>
                      Tags: {series.tags + ' '}<br/>
                      Rating: {series.rating + ' '}<br/>
                    </div>
                 </div>                
               )
             })}
         </div>
      </div>
  );
}

}

export default withRouter(Serieslist);