import React, { Component } from 'react';
// import axios from 'axios';
import {withRouter} from 'react-router-dom';


class RandomSeries extends Component {

  constructor(props){
    super(props);
    this.loadRandom = this.loadRandom.bind(this);
    
    this.state = {
  
    }

  };

  loadRandom(){
    //console.log(e.target.id);
    var rand = Math.floor(Math.random()*5);
    var listOfSeries = ['Inuyasha','Giants','Superman','Kubera','Usogui','Chainsawman'];
    var x = listOfSeries[rand];
    console.log(x);
    if(x === undefined){
      x = listOfSeries[0];
    }
    this.loadRandom2(x);
    // this.props.history.push({
    //   pathname: `/Series-List/Chapter-List/${x}`,
    // });
    
  }

  loadRandom2(x){
    this.props.history.push({
        pathname: `/Series-List/Chapter-List/${x}`,
    });

  };


  componentDidMount(){

  }

  render(){
      return (
      <div className="container-fluid">
         <div className="content">
            RandomSeries page
            <button onClick={this.loadRandom}>Random Me</button>
           
         </div>
      </div>
  );
}

}

export default withRouter(RandomSeries);